* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: none;
}

body {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  background-color: #d3d3d3;
  overscroll-behavior: none;
  gap: 8px;
}

aside,
section {
  background-color: #e3e3e3;
}

aside {
  width: 40%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 16px;
  padding: 12px;
  overflow: scroll;

  span.gesture-item {
    font-size: 24px;
    padding: 12px 0px;
  }

  // span[highlight] {
  //   font-size: 28px;
  //   color: red;
  // }
  span.highlight {
    font-size: 28px;
    color: red;
  }
}

main {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

#canvas {
  height: 80vh;
}

#label {
  height: 20vh;
  display: flex;
  align-items: center;
  gap: 8px;
  background-color: #d3d3d3;

  div {
    width: 100%;
    height: 100%;
    font-size: 24px;
    display: flex;
    flex-direction: column;
    gap: 32px;
    justify-content: center;
    align-items: center;
    background-color: #e3e3e3;

    span.label {
      font-size: 32px;
    }
  }
}
